.input-wrapper {
  width: 100%;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  border-bottom: 2px solid #fff;
  border-radius: 10px;
}
.input-wrapper.dark {
  border-color: #191919;
}
.input-wrapper textarea {
  height: 157px;
  resize: none;
}
.input-wrapper input,
.input-wrapper textarea {
  box-sizing: border-box;
  width: 67%;
  padding: 14px 15px;
  outline: none;
  border: none;
  color: #fff;
  font-family: "Varela", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: transparent;
}
.input-wrapper input:-webkit-autofill, .input-wrapper input:-webkit-autofill:hover, .input-wrapper input:-webkit-autofill:focus, .input-wrapper input:-webkit-autofill:active,
.input-wrapper textarea:-webkit-autofill,
.input-wrapper textarea:-webkit-autofill:hover,
.input-wrapper textarea:-webkit-autofill:focus,
.input-wrapper textarea:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #fff !important;
  caret-color: white;
}
.input-wrapper input.dark,
.input-wrapper textarea.dark {
  color: #191919;
}
.input-wrapper input.dark:-webkit-autofill, .input-wrapper input.dark:-webkit-autofill:hover, .input-wrapper input.dark:-webkit-autofill:focus, .input-wrapper input.dark:-webkit-autofill:active,
.input-wrapper textarea.dark:-webkit-autofill,
.input-wrapper textarea.dark:-webkit-autofill:hover,
.input-wrapper textarea.dark:-webkit-autofill:focus,
.input-wrapper textarea.dark:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #191919 !important;
  caret-color: #191919;
}
.input-wrapper label {
  margin: 0 16px 0 auto;
  cursor: pointer;
  font-weight: 300;
  font-size: 12px;
  line-height: 13px;
}
.input-wrapper input::placeholder,
.input-wrapper textarea::placeholder {
  color: rgba(255, 255, 255, 0.25);
}
.input-wrapper .dark::placeholder {
  color: #191919;
  opacity: 0.25;
}
.input-wrapper.not-valid {
  color: #e35353;
  border-bottom: 2px solid #e35353;
}
.input-wrapper.valid {
  border-bottom: 2px solid #ffd64d;
}
